/* eslint-disable no-new */
import Vue from 'vue';
import Slider from './Slider';

new Vue({
  el: '#amp-ca-slider',
  render(h) {
    return h(Slider, {
      props: {
        contentUrl: (this.$el.attributes['data-contenturl'] && this.$el.attributes['data-contenturl'].value.length > 0 ? this.$el.attributes['data-contenturl'].value : 'DEFAULT')
      }
    });
  }
});



