<template>
  <a :href="href">
    <img :src="content.url" :alt="content.altText"/>
  </a>
</template>

<script>
  export default {
    props: {
      content: {
        type: Object
      }
    },
    data() {
      return {
      }
    },
    computed: {
      href() {
        if (this.content.link) {
          return this.content.link
        } else {
          return '#'
        }
      }
    }
  }
</script>

<style lang="scss" scoped>
  img {
    width: 100%;
    height: 100%;
  }
</style>




